}

@-webkit-keyframes rotation {
from{-webkit-transform: rotateY(0deg);transform: rotateY(0deg);}
from{-webkit-transform: rotateY(360deg);transform: rotateY(360deg);}
}

@keyframes rotation {
from{-webkit-transform: rotateY(0deg);transform: rotateY(0deg);}
from{-webkit-transform: rotateY(360deg);transform: rotateY(360deg);}
} 

