  @media screen and (max-width:900px) {

    .accordion {
      overflow: hidden;
      width: 8em;
      border-radius: 3px;
      background: rgb(255, 168, 69);
  }
  
  .accordion-visible {   
      background-color: rgb(255, 153, 36);
      width: 100%;
      cursor: pointer;
      border: 1px solid #7c3c00;;
      display: flex;
      flex-direction: column;
      padding-top: 8%;
      padding-left: 5%;
      padding-bottom: 8%;
      padding-right: 8%;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
  }


  /* --------------toggle animation------------------------------------ */
  

  .line{
    background: rgb(255, 199, 108);
    width: 2em;
    height: 0.3em;
    margin-top: 0.3em;
    border-radius: 10px; 
    transition:  0.4s;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    margin-left: 45%;
  }
  .line:nth-child(3n +1 ){
    transition:  1s;
  }

  .line:nth-child(3n +3 ){
    transition:  1s;
  }

  .animated-line:nth-child(3n +1 ){
    background: rgb(88, 88, 88);
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    width: 2em;
    height: 0.3em;
    margin-top: 0.3em;
    transition:1s;
  }
  .animated-line:nth-child(3n +2 ){
    /* background: rgb(255, 199, 108); */
    background: rgb(88, 88, 88);
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
    width: 2em;
    height: 0.3em;
    margin-top: 0.3em;
    transition:0.4s;
    /* transform: rotate(-270deg);
    width: 2em; */
  }

  .animated-line:nth-child(3n +3 ){
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
    background: rgb(88, 88, 88);
    height: 0.3em;
    transition:1s;
    margin-top: 0.3em;
  }


/* ----------------------------------------------------- */

  .accordion-toggle {
      height: 0px;
      font-size: 18px;
      opacity: 0;  
      transition:  
      opacity 0.3s ease-in-out,
      height 0.3s 0.3s ease-in-out;
      color: #333;
      border-bottom-left-radius: 3px;
      border-bottom-right-radius: 3px;
      
  }
  
  .animated {
      opacity: 1;
      transition:  
      height 0.3s ease-in-out,
      opacity 0.3s 0.3s ease-in-out;
  }
  
    .accordion-toggle p {
        margin: 0;
        padding: 1.3em 0.5em;
        /* pointer-events: none; */
        line-height: 1.3;
        border-bottom: black;
        text-decoration: none;
        border-bottom:  1px solid rgb(82, 82, 82);
        text-align: center;
        padding-top: 1.2em;
        padding-bottom: 1.2em;
      
    }

      .link{
        color: rgb(80, 80, 80);
        
        
      }
      .link:hover {
        color: rgb(224, 138, 0);
        
      }
      

  }



  @media screen and (min-width:900px) {
    .accordion{
        display: none;
    }
 }
 

