.box{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;

    margin-left: 20vw;
    margin-right: 20vw;
    margin-top: 10vh ;
    color: #c2c2c2;
    border-bottom: 0.1px solid #8b8b8b69;
    border-top: 0.1px solid #8b8b8b69;

  }

 /* -----------------------------------------  */
.one{

  margin-bottom: 5em;
  margin-top: 1.4em;
  margin-left: 1.7em;
}
.logo-box{
  width: 3em;
  display: inline-block;
}

[data-name]{
  display: inline-block;
  font-size: 2.3em ;
}

[data-price]{
  margin-left: 2em;
  display: inline-block;
  font-size: 1.8em;
  font-weight: 900;
}
[data-per]{
  display: inline-block;
}

.two{
  display: grid;
  grid-template-columns: 50%  50%  ;
}

.two h4{
  color: rgb(110, 110, 110);
  margin-top: 1.4em;
}
.two h3{
  color: #c2c2c2;
}

.three{
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 color: rgb(255, 255, 255);
 
}
.four{

  display: flex;
  align-items: center;
  justify-content: center;
}

/* -------------------------------------------- */

input{
  font-size: 1em;
}

.equal{
  font-size: 2rem;
  font: bold;
}

input{
  border-radius: 0.2em;
  padding:0.2em;
  width: 8em;
}

/* -------------------------------- */
.green{
  color: rgb(30, 149, 30);
  font-weight: 900;

}

.red{
  color: rgb(199, 37, 37);
  font-weight: 900;
}
/* -------------------------------- */

.side-img {
  width: 60%;

	animation: rotation ;
  animation-duration: 8s;
  animation-iteration-count:infinite;
  animation-timing-function: ease-out;
}

@keyframes rotation {
from{transform: rotateY(0deg);}
from{transform: rotateY(360deg);}
} 

/* --------------------------------- */


  /* ----responsive------ */

  @media screen and (max-width:900px) {

    .logo-box{
      width: 2.1em; 
    }
    
    [data-name]{
      font-size: 1.5em ;
    }
    
    [data-price]{
      margin-left: 1.5em;
      font-size: 1.2em;
    }
  
        .two h4{
          font-size: 0.9em;
        }
        .two h3{
          font-size: 1em;
        }
        .supply{
          margin-left: 1.5em;
        }

    }

    @media screen and (max-width:420px) {

      .box{
        
        grid-template-columns: 100% ;
        /* grid-template-rows: 50%; */
      }

      .two{
        display: none;
      }

      .side-img {
        display: none;
      }
  
      }
    
  
  
  